/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable react/no-array-index-key */
import React, { useEffect, useState } from "react";
import Apps from "./Apps";
import appIcon from "../../../assets/appIconHover.svg";
import axios from "axios";
import CategoryApps from "./CategoryApps";
import FunctionApps from "./FunctionApps";
import { Button, createTheme, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Grid, TextField } from "@mui/material";
import { green } from "@mui/material/colors";
import { ThemeProvider } from "@emotion/react";
import PopularApps from "../filteredApps/PopularApps";
import TopPicks from "../filteredApps/TopPicks";
import WhatsNew from "../filteredApps/WhatsNew";
import { getUser } from "../../../helpers/Utils";

const response = {
  firstName: "",
  lastName: "",
  email: "",
  appName: "",
  appDesc: "",
  githubUrl: ""
}

const AppList = () => {
  const [searchTerm, setSearchTerm] = useState("");
  const [selected, setSelected] = useState("All Collections");
  const [moreClicked, setMoreClicked] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [categoryLoading, setCategoryLoading] = useState(false);
  const [functionLoading, setFunctionLoading] = useState(false);
  const [allCategories, setAllCategories] = useState([]);
  const [allFunctions, setAllFunctions] = useState([]);
  const [categoryResults, setCategoryResults] = useState({});
  const [functionResults, setFunctionResults] = useState({});

  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [appName, setAppName] = useState('');
  const [appDesc, setAppDesc] = useState('');
  const [github, setGithub] = useState('');

  const [idsPopular, setIdsPopular] = useState([]);
  const [idsTop, setIdsTop] = useState([]);
  const [idsNew, setIdsNew] = useState([]);

  const [res, setRes] = useState(response);
  const [open, setOpen] = React.useState(false);

  const [user, setUser] = useState({});

  const getAllApps = async () => {
    setIsLoading(true);
    const res1 = await axios.get("/platform/api/app/allCategories");
    const res2 = await axios.get("/platform/api/app/allFunctions");
    console.log(res1.data, res2.data)
    // const data1 = res1.data.filter((i) => {
    //   return i.fields.BuiltBy !== 'naas';
    // })
    // const data2 = res2.data.filter((i) => {
    //   return i.fields.BuiltBy === 'Katonic';
    // })
    setAllCategories(res1.data);
    setAllFunctions(res2.data);
    setIsLoading(false);
  };

  const handleInputChange = (ev) => {
    setRes({
      ...res,
      [ev.target.name]: ev.target.value
    })
  }

  const theme = createTheme({
    palette: {
      primary: {
        main: '#5c31ff',
      },
      secondary: {
        main: green[400],
      },
    },
  });


  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  useEffect(() => {
    console.log(selected)
    const result1 = allCategories.reduce((acc, item) => {
      if (item.fields.Category === selected) {
        acc = item;
      }
      return acc;
    }, {});

    const result2 = allFunctions.reduce((acc, item) => {
      if (item.fields.Function === selected) {
        acc = item;
      }
      return acc;
    }, {});
    setCategoryResults(result1);
    setFunctionResults(result2);
    if (selected !== "All Collections" && selected !== "" && categoryOptions.includes(selected)) {
      setCategoryLoading(true);
    }

    if (selected !== "All Collections" && selected !== "" && appFeatureOptions.includes(selected)) {
      setFunctionLoading(true);
    }
    return () => {
      setCategoryLoading(false);
      setFunctionLoading(false);
    };

  }, [selected]);

  useEffect(() => {
    getAllApps();
    getPopularApps()
    getTopApps()
    getNewApps()
  }, []);

  const sendMail = async () => {
    handleClose()
    const contactBody = {
      "firstName": user.firstName,
      "lastName": user.lastName,
      "email": user.email,
      "githubUrl": github,
      "appName": appName,
      "appDesc": appDesc
    }
    await fetch('/platform/api/app/addcontact',
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(contactBody)
      })
      .then(res => console.log(res))
      .catch(err => console.error(err))

    const mailBody = {
      "firstName": user.firstName,
      "toSend": user.email,
      "appName": appName,
      "githubUrl": github
    }
    await fetch(
      '/platform/api/app/sendMail',
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(mailBody)
      }
    )
  }

  const featuredOptions = [
    "All Collections",
    "Popular",
    "Top Picks",
    "Whats New",
  ];

  const categoryOptions = [
    "Airlines",
    "Automotive",
    "Banking & Financial Services",
    "Cloud Network",
    // "Energy",
    "HealthCare",
    "Insurance",
    "Logistics & Supply Chain",
    "Manufacturing",
    "Media & Entertainment",
    // "Pharmaceuticals",
    // "Public Sector & Nonprofits",
    // "Real Estate",
    "Retail & CPG",
    "Telecom",
    "Technology",
    "Transportation",
  ];

  const appFeatureOptions = [
    "Accounting & Finance",
    "Compliance",
    "Customer Service",
    "Human Resource",
    "IT Operations",
    "Marketing",
    "Productivity",
    "Risk & Security",
  ];

  const getPopularApps = async () => {
    const id = "recFfMRFil5PXHJij"
    const body = {
      "id": id
    }
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(body)
    }
    await fetch('/platform/api/app/searchFeaturedById', options)
      .then(res => res.json())
      .then(data => setIdsPopular(data.fields.Apps))
  }

  const getTopApps = async () => {
    const id = "recEhmibXawVg0dLm"
    const body = {
      "id": id
    }
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(body)
    }
    await fetch('/platform/api/app/searchFeaturedById', options)
      .then(res => res.json())
      .then(data => setIdsTop(data.fields.Apps))
  }

  const getNewApps = async () => {
    const id = "reclht6EA3NwAqta5"
    const body = {
      "id": id
    }
    const options = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(body)
    }
    await fetch('/platform/api/app/searchFeaturedById', options)
      .then(res => res.json())
      .then(data => setIdsNew(data.fields.Apps))
  }

  const renderCategory = () => {
    if (selected === "All Collections" || selected === "" || searchTerm !== "")
      return <Apps search={searchTerm} />;
    else if (selected === 'Popular')
      return <PopularApps ids={idsPopular} />
    else if (selected === 'Top Picks')
      return <TopPicks ids={idsTop} />
    else if (selected === 'Whats New') {
      return <WhatsNew ids={idsNew} />
    }
    else if (categoryLoading === true && categoryOptions.includes(selected)) {
      console.log(categoryResults)
      return <CategoryApps data={categoryResults} />;
    }
    else if (functionLoading === true && appFeatureOptions.includes(selected)) {
      console.log(functionResults)
      return <FunctionApps data={functionResults} />;
    } else {
      return <h1>Cannot Find</h1>
    }
  };

  return (
    <>
      {isLoading ? (
        <div className="loading" />
      ) : (
        <div className="app">
          <div className="appHead">
            {/* <div className="appHeadLeft">
              <h1 style={{ fontSize: "23px" }} className="appHeading">
                {" "}
                <i
                  className="simple-icon-grid"
                  style={{ color: '#5c31ff', marginRight: "5px" }}
                />{" "}
                App Store
              </h1>
              <p className="appDesc">
                Marketplace of Solution Accelerators including Pre Built Code,
                Notebooks, and step by step instructions to get started.
              </p>
              <button
                onClick={handleClickOpen}
                style={{
                  fontSize: "15px",
                  color: "#5c31ff",
                  marginTop: "15px",
                  marginBottom: "-10px",
                  border: 'none'
                }}
              >
                <i className="iconsminds-open-book" /> Submit to App Catalogue
              </button>
            </div> */}
            {/* <div className="appHeadRight">
              <div className="inputBox">
                <i className="simple-icon-magnifier" />
                <input
                  placeholder="Search for apps..."
                  className="appSearch"
                  type="text"
                  onChange={(e) => setSearchTerm(e.target.value)}
                  value={searchTerm}
                />
              </div>
            </div> */}

            <div class="container content-space-1 contenspace-t-md-0">
              <div class="row justify-content-md-between align-items-md-center">
                <div class="col-md-5 mb-7 mb-md-0">
                  <div class="mb-4">
                    <h1 className="apps-h1">Add apps, get work done</h1>
                    <p>Find tools to improve your workflow - right within Front.</p>
                  </div>

                  <form>
                    <div class="input-card border">
                      <div class="input-card-form">
                        {/* <label for="searchAppsForm" class="form-label visually-hidden">Search for apps</label> */}
                        <input
                          placeholder="Search for apps..."
                          className="appSearch"
                          type="text"
                          onChange={(e) => setSearchTerm(e.target.value)}
                          value={searchTerm}
                        />
                      </div>
                      <button type="button" class="btn btn-primary btn-primary-search">
                        <i class="simple-icon-magnifier"></i>
                      </button>
                    </div>
                  </form>
                </div>

                <div class="col-md-5">
                  <img class="img-fluid" src="https://htmlstream.com/front/assets/svg/illustrations/oc-on-the-go.svg" alt="Image Descriptio" />
                </div>
              </div>
            </div>
          </div>
          <div className="appContent">
            <div className={moreClicked ? "appLeftScroll" : "appLeft"}>
              <div className="filterDiv">
                <h4>Featured</h4>
                <ul className="appSectionList">
                  {featuredOptions.map((option) => (
                    <li
                      onClick={() => setSelected(option)}
                    >{option}</li>
                  ))}
                </ul>
                <h4>Categories</h4>
                <ul className="appSectionList">
                  {categoryOptions.slice(0, 5).map((option) => (
                    <li onClick={() => setSelected(option)}>{option}</li>
                  ))}
                  <li
                    id={moreClicked ? "showMore" : "showMoreNow"}
                    onClick={() => setMoreClicked(true)}
                  >
                    {moreClicked === false && "Show More..."}
                  </li>
                  {moreClicked &&
                    categoryOptions
                      .slice(5, 16)
                      .map((option) => (
                        <li onClick={() => setSelected(option)}>{option}</li>
                      ))}
                  <li
                    style={{ color: "#5c31ff" }}
                    onClick={() => setMoreClicked(false)}
                  >
                    {moreClicked && "Show Less..."}
                  </li>
                </ul>
                <h4>Functions</h4>
                <ul className="appSectionList">
                  {appFeatureOptions.map((option) => (
                    <li onClick={() => setSelected(option)}>{option}</li>
                  ))}
                </ul>
              </div>
            </div>
            <div className="appRight">
              {isLoading ? <div className="loading" /> : renderCategory()}
            </div>
          </div>
        </div>
      )}
      <ThemeProvider theme={theme}>
        <Dialog open={open} onClose={handleClose}>
          <DialogTitle>App Suggestion</DialogTitle>
          <DialogContent>
            <DialogContentText>
              Submit your app suggestions to the Katonic team and we will review it as soon as possible.
            </DialogContentText>

            <Grid container spacing={2}>
              {/* <Grid item xs={6}>
                <TextField
                  autoFocus
                  margin="dense"
                  id="name"
                  label="First Name"
                  type="text"
                  fullWidth
                  variant="standard"
                  name="firstName"
                  value={firstName}
                  onChange={(e) => setFirstName(e.target.value)}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  
                  margin="dense"
                  id="name"
                  label="Last Name"
                  type="text"
                  fullWidth
                  variant="standard"
                  name="lastName"
                  value={lastName}
                  onChange={(e) => setLastName(e.target.value)}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  
                  margin="dense"
                  id="name"
                  label="Email Address"
                  type="email"
                  fullWidth
                  variant="standard"
                  name="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </Grid> */}
              <Grid item xs={12}>
                <TextField

                  margin="dense"
                  id="name"
                  label="App Name"
                  type="text"
                  fullWidth
                  variant="standard"
                  name="appName"
                  value={appName}
                  onChange={(e) => setAppName(e.target.value)}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField

                  margin="dense"
                  id="name"
                  label="App Description"
                  multiline={true}
                  minRows={3}
                  type="text"
                  fullWidth
                  variant="standard"
                  name="appDesc"
                  value={appDesc}
                  onChange={(e) => setAppDesc(e.target.value)}
                />
              </Grid>
              <Grid item xs={12}>
                <TextField

                  margin="dense"
                  id="name"
                  label="Link to Github"
                  type="text"
                  fullWidth
                  variant="standard"
                  name="githubUrl"
                  value={github}
                  onChange={(e) => setGithub(e.target.value)}
                />
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Cancel</Button>
            <Button variant="contained" onClick={sendMail}>Submit</Button>
          </DialogActions>
        </Dialog>
      </ThemeProvider>
    </>
  );
};

export default AppList;
