import React, { useEffect, useState } from "react";
import { NavLink, useHistory } from "react-router-dom";
import { adminRoot } from "../../../constants/defaultValues";
import { featuredApps, popularApps } from "../../../constants/dummyApps";
import { data } from "../../../constants/featuredBox";
import Dialog from "@mui/material/Dialog";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";
import ReportIcon from "@mui/icons-material/Report";
import CodeIcon from "@mui/icons-material/Code";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import { Button, DialogActions, TextareaAutosize, TextField } from "@mui/material";
import { addFeedback, getUser } from "../../../helpers/Utils";


const Apps = ({ search }) => {
  const history = useHistory();
  const [isOpen, setIsOpen] = useState(false);
  const [open, setOpen] = useState(false);
  const [datas, setDatas] = useState({});
  const [apps, setApps] = useState()

  const [user, setUser] = useState({});


  const searchFeaturedApps = () => {
    const filteredApps = featuredApps.filter((app) => {
      if (search === "") {
        return app;
      } else if (app.fields.Name.toLowerCase().includes(search.toLowerCase())) {
        return app;
      }
    });
    return filteredApps;
  };

  const getCategory = (id) => {
    const body = {
      id: id
    }
    const options = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(body)
    }
    return fetch(`platform/api/app/searchCategoryById`, options)
      .then(res => res.json())
  }

  const getAllApps = () => {
    const options = {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    }
    return fetch("/platform/api/app/all", options)
      .then(res => res.json())
      .then(data => setApps(data))
  }

  const redirect = (id) => {
    history.push(`${adminRoot}/apps/appcollection/${id}`);
  };

  const redirectGroup = (id) => {
    history.push(`${adminRoot}/apps/appcollection/${id}`);
  };

  const searchPopularApps = () => {
    const filteredApps = popularApps.filter((app) => {
      if (search === "") {
        return app;
      } else if (app.fields.Name.toLowerCase().includes(search.toLowerCase())) {
        return app;
      }
    });
    return filteredApps;
  };
  useEffect(() => {
    searchFeaturedApps();
    searchPopularApps();
  }, [search]);

  useEffect(() => {
    getAllApps();
  }, [])

  const NoResult = () => {
    return (
      <div className="noResult">
        <img
          src="https://static.thenounproject.com/png/385145-200.png"
          alt=""
        />
        <h5>Sorry, we couldn't find any results!</h5>
      </div>
    );
  };

  const FeedbackForm = () => {
    const [feedback, setFeedback] = useState('');

    const handleClickOpen = () => {
      setOpen(true);
    };

    const handleClose = () => {
      setOpen(false);
    };

    const handleSubmit = () => {
      addFeedback(user, feedback, datas.fields.Name);
      setOpen(false);
    }

    return (
      <div>
        <Dialog fullWidth={true} maxWidth='md' open={open} onClose={handleClose}>
          <DialogTitle>Feedback Form</DialogTitle>
          <DialogContent>

            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Enter your Feedback"
              multiline={true}
              minRows={4}
              type="text"
              fullWidth
              variant="standard"
              name="appDesc"
              value={feedback}
              onChange={(e) => setFeedback(e.target.value)}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Cancel</Button>
            <Button onClick={handleSubmit}>Submit Feedback</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }

  const AppDialog = () => {
    const theme = createTheme({
      components: {
        // Name of the component
        MuiDialogTitle: {
          styleOverrides: {
            // Name of the slot
            root: {
              padding: 0,
              borderBottom: "1px solid lightgray",
            },
          },
        },
        MuiDialogContent: {
          styleOverrides: {
            root: {
              padding: 0,
            },
          },
        },
      },
    });

    const [scroll, setScroll] = React.useState("body");
    setIsOpen(true);

    const handleClose = () => {
      setIsOpen(false);
      setOpen(false)
      setDatas({});
    };

    const descriptionElementRef = React.useRef(null);
    React.useEffect(() => {
      if (isOpen) {
        const { current: descriptionElement } = descriptionElementRef;
        if (descriptionElement !== null) {
          descriptionElement.focus();
        }
      }
    }, [isOpen]);

    return (
      <>
        <ThemeProvider theme={theme}>
          <div>
            <Dialog
              sx={{
                height: "100vh",
                margin: "0 auto",
                overflow: "hidden",
              }}
              open={isOpen}
              maxWidth={"lg"}
              onClose={handleClose}
              scroll={scroll}
              aria-labelledby="scroll-dialog-title"
              aria-describedby="scroll-dialog-description"
              className="dialogBox"
            >
              <DialogTitle id="scroll-dialog-title">
                <div className="dialogTop">
                  <img src={datas.fields.AppICON[0].url} alt="" />
                  <div className="topDesc">
                    <h3>{datas.fields.Name}</h3>
                    <p className="shortDesc">{datas.fields.ShortDescription}</p>
                    <div className="categoryDesc">
                      <p>
                      <img
                        className="pricingIcon"
                        src={datas.fields.PricingICON[0].thumbnails.small.url}
                        alt="logo"
                      />
                        Built by {datas.fields.BuiltBy}
                      </p>
                      <p>
                      <img
                        className="pricingIcon"
                        src={datas.fields.PricingICON[0].thumbnails.small.url}
                        alt="logo"
                      />
                        {datas.fields.Pricing}
                      </p>
                    </div>
                  </div>
                  <span className="closeSpan" onClick={handleClose}>
                    x
                  </span>
                </div>
              </DialogTitle>
              <DialogContent dividers={scroll === "body"}>
                <DialogContentText
                  id="scroll-dialog-description"
                  ref={descriptionElementRef}
                  tabIndex={-1}
                >
                  <div className="appDialogContent">
                    <div className="appContentTop">
                      <div className="contentLeft">
                        <h1>App Description</h1>
                        <p>
                          {datas.fields.LongDescription}
                        </p>
                        {/* <div className="appAudience">
                        <h4>
                          Audience :
                          <span className="audienceList">
                            Business Analyst, Marketing Professional, Customer
                            Access, App Developer
                          </span>
                        </h4>
                      </div> */}
                        <a
                          target="_blank"
                          rel="noreferrer"
                          href="https://katonic.ai/signup.html"
                        >
                          Learn more about {datas.fields.Name} App here
                        </a>
                      </div>
                      <div className="contentRight">
                        <h4>Try this yourself</h4>
                        <p style={{ fontSize: "15px" }}>
                                                  </p>
                        <ol>
                          <li>
                            Get access to the notebook: Modify the code, add lines, and make it yours.
                          </li>
                          <li>
                            Review the Prediction Results: Customise the results with your work data.
                          </li>
                          <li>
                            Interact with model with the Prebuilt App. Modify and showcase the app with others.
                          </li>
                        </ol>
                        <button className="feedbackBtn">
                          <ReportIcon /> <a rel="noreferrer" target="_blank" href="https://katonic.ai/signup.html">Send Feedback</a>
                        </button>
                        {datas.fields.NotebookExists && <button className="feedbackBtn">
                          <CodeIcon /> <a rel="noreferrer" target="_blank" href="https://katonic.ai/signup.html">Launch Notebook</a>
                        </button>}

                        {datas.fields.AppExists && <button className="feedbackBtn">
                          <AddCircleIcon /> <a rel="noreferrer" target="_blank" href="https://katonic.ai/signup.html">Launch App</a>
                        </button>}
                        {
                          datas.fields.Category > 0
                          &&
                          <>
                            <h4 style={{ marginBottom: "10px" }}>Categories</h4>
                            <span className="categorySpan">{getCategory(datas.fields.id)}</span>
                          </>
                        }
                        {/* <video
                        style={{ marginTop: "20px" }}
                        width="320"
                        height="240"
                        controls
                      >
                        <source
                          src="https://www.youtube.com/watch?v=cGl5CqSiPLw"
                          type="video/webm"
                        />
                      </video> */}
                      </div>
                    </div>
                    {/* <div className="appContentBottom">
                    <h3 style={{ marginTop: "20px", marginLeft: "20px" }}>
                      People who viewed {datas.fields.Name} also viewed
                    </h3>
                    <div className="alsoViewed">
                      {popularApps.map((item) => {
                        return (
                          <div className="smallAppPopup">
                            <img
                              className="appLogo"
                              key={item}
                              src={item.fields.AppICON[0].url}
                              alt="asg"
                            />
                            <div className="appSmallInfo">
                              <h4>{item.fields.Name}</h4>
                              <p>{item.fields.ShortDescription.length >= 50 ? item.fields.ShortDescription.slice(0, 50) + "..." : item.fields.ShortDescription}</p>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div> */}
                  </div>
                </DialogContentText>
              </DialogContent>
            </Dialog>
          </div>
        </ThemeProvider>

      </>
    );
  };

  return (
    <>
      <div className={search === "" ? "appTop" : "appTop noAppTop"}>
        {search === ""
          ? data.map((item, index) => (
            <div onClick={() => redirect(item.id)} className="appCard">
              <img className="appImg" src={item.image} alt="" />
              <div className="content">
                <h3
                  className={index === 2 ? "itemHeadingWhite" : "itemHeading"}
                >
                  {item.heading1}
                </h3>
                <br />
                <h3
                  className={
                    index === 2 ? "belowHeadingWhite" : "belowHeading"
                  }
                >
                  {item.heading2}
                </h3>
                <NavLink
                  className={index === 2 ? "contentTagWhite" : "contentTag"}
                  to={`${adminRoot}/apps/appcollection/${item.id}`}
                >
                  View Collection <span>&#8594;</span>
                </NavLink>
              </div>
            </div>
          ))
          : null}
      </div>
      {search === "" ?
        <div className="appBottom">
          {searchFeaturedApps().length === 0 ? null : (
            <h2>You recently viewed</h2>
          )}
          <div className="smallAppList">
            {searchFeaturedApps().length === 0 &&
              searchPopularApps().length === 0 ? (
              <NoResult />
            ) : (
              searchFeaturedApps().filter((item) => item.fields.BuiltBy === 'Katonic').map((app) => {
                return (
                  <div
                    onClick={
                      app.isGroup
                        ? () => redirectGroup(app.id)
                        : () => {
                          setIsOpen(true);
                          setDatas(app);
                        }
                    }
                    className="smallApp"
                  >
                    <div className="appSmallInfo">
                      <img
                        className="appLogo"
                        key={app}
                        src={app.fields.CategoryIcon[0].url}
                        alt="asg"
                      />
                      <h4>{app.fields.Name?.length >= 30 ? app.fields.Name.slice(0, 30) + "..." : app.fields.Name}</h4>
                      <p>{app.fields.ShortDescription?.length >= 30 ? app.fields.ShortDescription.slice(0, 30) + "..." : app.fields.ShortDescription}</p>
                    </div>
                    <div>
                      {app.fields.Pricing === 'Paid' && <p className="pricingTag">{app.fields.Pricing}</p>}
                    </div>
                  </div>
                );
              })
            )}
          </div>
          {searchPopularApps().length === 0 ? null : <h2>Popular</h2>}
          <div className="smallAppList">
            {searchPopularApps().filter((item) => item.fields.BuiltBy === 'Katonic').map((app) => {
              return (
                <div
                  onClick={
                    app.isGroup
                      ? () => redirectGroup(app.id)
                      : () => {
                        setIsOpen(true);
                        setDatas(app);
                      }
                  }
                  className="smallApp"
                >
                  <div className="appSmallInfo">
                    <img
                      className="appLogo"
                      key={app}
                      src={app.fields.CategoryIcon[0].thumbnails.large.url}
                      alt="asg"

                    />
                    <h4>{app.fields.Name?.length >= 30 ? app.fields.Name.slice(0, 30) + "..." : app.fields.Name}</h4>
                    <p>{app.fields.ShortDescription?.length >= 30 ? app.fields.ShortDescription.slice(0, 30) + "..." : app.fields.ShortDescription}</p>
                  </div>
                  <div>
                    {app.fields.Pricing === 'Paid' && <p className="pricingTag">{app.fields.Pricing}</p>}
                  </div>
                </div>
              );
            })}
          </div>

          {isOpen && <AppDialog />}
          {open && <FeedbackForm />}
        </div>
        :
        (
          <div className="appBottom">
            {searchFeaturedApps().length === 0 ? null : (
              <h2>Search</h2>
            )}
            <div className="smallAppList">
              {searchFeaturedApps().length === 0 &&
                searchPopularApps().length === 0 ? (
                <NoResult />
              ) : (
                searchFeaturedApps().map((app) => {
                  return (
                    <div
                      onClick={
                        app.isGroup
                          ? () => redirectGroup(app.id)
                          : () => {
                            setIsOpen(true);
                            setDatas(app);
                          }
                      }
                      className="smallApp"
                    >
                      <img className="appLogo" key={app} src={app.fields.CategoryIcon[0].thumbnails.large.url} alt="asg" />
                      <div className="appSmallInfo">
                        <h4>{app.fields.Name?.length >= 30 ? app.fields.Name.slice(0, 30) + "..." : app.fields.Name}</h4>
                        <p>{app.fields.ShortDescription?.length >= 30 ? app.fields.ShortDescription.slice(0, 30) + "..." : app.fields.ShortDescription}</p>

                      </div>
                      <div>
                        {app.fields.Pricing === 'Paid' && <p className="pricingTag">{app.fields.Pricing}</p>}
                      </div>
                    </div>
                  );
                })
              )}
            </div>

            {isOpen && <AppDialog />}
            {console.log(open)}
            {open && <FeedbackForm />}
          </div>
        )}
    </>
  );
};

export default Apps;
